/* app.css */

.container {
  flex: 1;
  padding: 20px;
}

.user-rectangle {
  width: 180px;
  height: 80px;
  border-radius: 10px;
  background-color: #007BFF; /* Mavi arka plan */
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  padding: 10px;
  color: white;
  font-size: 14px;
  font-weight: bold;
  position: relative;
}

.user-rectangle .email {
  margin-left: 10px;
}

.user-rectangle .menu-icon {
  cursor: pointer;
  font-size: 20px;
  position: absolute;
  bottom: 10px;
  right: 10px;
  color: white;
}

.user-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

/* app.css */
.context-menu {
  position: absolute;
  background-color: #ffffff;
  border: 1px solid #ddd;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  width: 180px;
  border-radius: 8px;
  padding: 10px;
  display: block; /* Görünürlüğü ayarla */
}

.context-menu.visible {
  display: block; /* Görünür olduğunda menüyü göster */
}


.context-menu-item {
  padding: 8px 12px;
  cursor: pointer;
  font-size: 14px;
  color: #333;
  display: flex;
  align-items: center;
}

.context-menu-item:hover {
  background-color: #f0f0f0;
}

.context-menu-item .icon {
  margin-right: 10px;
}
